.tile {
  margin-bottom: 30px;
  text-align: center;
  padding: 10px;
}

.tile .bg-1, .tile .bg-2 {
  padding: 10px;
  border-radius: 15px;
  border: 1px solid #CCC;
}

.tile a {
  color: #000;
  min-height: 45px;
  font-size: 1.4rem;
  text-align: left;
}


.tile .btn {
  background-color: #a9f4bd;
}

.frontpage .tilecontent {
  border-radius: 10px;
  background-image: url(/static/img/region/skyline.svg);
  background-repeat: no-repeat;
  background-position: -10px;
  background-size: 100% auto;
  min-height: 120px;
}

.frontpage .tilecontent a, .tiles-category .tilecontent a {
  border-radius: 10px;
  color: #fff;
  height: 120px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  font-weight: bold;
}

.frontpage .tilecontent span {
  margin-top: auto;
  padding: 0 0 10px 10px;
}

.region h2 {
  border-bottom: 1px solid #CCC;
}

.tiles-category .tile span {
  color: #fff;
  font-weight: 700;
  font-size: 1.2rem;
  text-align: left;
  margin: 10px;
}

.tiles-category .tilecontent {
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 62px;
}

.tiles-category .tile:nth-child(5n) a {
	background: linear-gradient(45deg, rgba(245, 124, 0, 1) 15%, rgba(245, 124, 0, .8) 85%);
}
.tiles-category .tile:nth-child(5n+1) a {
	background: linear-gradient(45deg, rgba(194, 24, 91, 1) 15%, rgba(194, 24, 91, .8) 85%);
}
.tiles-category .tile:nth-child(5n+2) a {
	background: linear-gradient(45deg, rgba(25, 118, 210, 1) 15%, rgba(25, 118, 210, .8) 85%);
}
.tiles-category .tile:nth-child(5n+3) a {
	background: linear-gradient(45deg, rgba(0, 121, 107, 1) 15%, rgba(0, 121, 107, .8) 85%);
}
.tiles-category .tile:nth-child(5n+4) a {
	background: linear-gradient(45deg, rgba(56, 142, 60, 1) 15%, rgba(56, 142, 60, .8) 85%);
}

