  border-bottom: 1px solid #CCC; }

#header-wrapper a {
  color: #000; }

#content-wrapper {
  padding-top: 25px;
  margin-bottom: 50px; }

#content {
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #FFFFFF; }

#flash-messages {
