  content: none;
}*/
#content .bootstrap-select a, #content .bootstrap-select a:hover, #content .bootstrap-select a:visited, #content .bootstrap-select a:active {
  color: #000; }

#content .pagination .page-item.disabled a.page-link {
  color: #dee2e6; }

.search-table-result-header-text {
  padding-top: 0.5rem; }

.sortable {
  cursor: pointer; }

.sortable.active:after {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 0.255em;
  vertical-align: 0.255em;
  content: "";
  border-right: 0.3em solid transparent;
  border-left: 0.3em solid transparent; }

.sortable.active.asc:after {
  border-top: 0.3em solid;
  border-bottom: 0; }

.sortable.active.desc:after {
  border-top: 0;
  border-bottom: 0.3em solid; }

#content a.btn-icon {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  text-align: center;
  margin: 0 7px 7px 0; }

#content a.btn-icon:hover {
  border-radius: 10px; }

#content .nobreak {
  white-space: nowrap; }

#store-map-box {
  height: 600px;
  position: relative;
  margin-bottom: 30px; }

#store-map-overlay {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  bottom: 0;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.5);
  background: #F3F3F3;
  color: #333;
  padding: 10px;
  overflow-y: scroll;
  overflow-x: hidden; }

.overlay-close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  z-index: 2000; }

.map-not-revisited {
  font-style: italic; }

#opening-times-form .fa {
  color: #999;
  cursor: pointer; }

.tile {
